
html, body{height: 100%
}
body{
    position: relative;
    background-color: #f7f7f7;
}
section{
  padding: 0 30px;
}
.navbar{
  background-color: #3a393a;
}
[role="main"] {
    margin-left: 180px
}
.loading{
    text-align: center;
    position: absolute;
    bottom:0;
    right:0;
    font-size: 10px;
    background-color: #ccc;
    border: 1px solid #ddd;
    padding: 2px 5px;
}

.logo{
  font-size: 20px;
  color:#fff;
  text-align: center;
  background-color: #292929;
  padding: 20px 0;
}
.sidebar {
    width: 180px;
    background: #3a393a;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    z-index: 100;
    -webkit-box-shadow: inset -1px 0 0 rgba(0, 0, 0, .1);
            box-shadow: inset -1px 0 0 rgba(0, 0, 0, .1);
}
.sidebar .nav-link{
    color: #ecf8fc;
}
.sidebar .nav-link:hover{
    background-color: #27b756;
}
.sidebar-sticky{
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    height: 100vh;
    overflow-x: hidden;
    overflow-y: auto;
}

.tabelas{
  padding-top: 20px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.tabelas .item{
  width: 47%;
  text-align: left;
}
.tabelas .item i{font-size:14px
}
.titulo span{
  text-transform: uppercase;
  font-weight: bold;
  font-size: 15px;
  color: #67472b;
  display: inline-block;
}
.inferior{
  padding-top: 30px;
  padding-bottom: 30px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.item{
  background-color: #fff;
  padding: 20px;
  width: 22%;
  text-align: center;
  margin-right: 3%;
}
.item i{font-size: 30px;
}
.item .numero{font-size: 40px
}
.item .detalhe{color:#27b756
}
.item .discreto{font-size: 11px; color: #aaa
}





























































